.overlay {
  opacity: 0;
  visibility: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 20;
}

//    

.modal__cross {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 12px;
  right: 12px;
  fill: $color-text-secondary;
  @extend %cursor;

  @include tablet {
    width: 24px;
    height: 24px;
  }
}

//    

.modal__title {
  font-family: 'Seymour One', sans-serif;
  font-size: 16px;
  line-height: 1.5;
  text-align: center;
  color: $color-text-secondary;

  @include tablet {
    font-size: 20px;
    line-height: 1.4;
  }
}

.modal.active,
.overlay.active {
  opacity: 1;
  visibility: visible;
}

//   

.modal {
  opacity: 0;
  visibility: hidden;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 30;

  background-color: $color-modal-bg;

  border-radius: 10px;
  padding-top: 28px;
  padding-bottom: 28px;
  padding-left: 28px;
  padding-right: 28px;

  @include tablet {
    padding-top: 32px;
    padding-bottom: 32px;
    padding-left: 60px;
    padding-right: 60px;
  }

  //       
  //       
  //   width:
}

.modal__submit-btn {
  padding: 12px 34px;
  color: $color-text-secondary;
  background-color: $color-accent;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.25;
  border-radius: 10px;
  border: none;
  @extend %cursor;

  @include tablet {
    font-size: 14px;
    line-height: 1.21;
  }
}

